@mixin btn_hover {
  &:hover {
    background-image: none;
    background-color: $main-color;
  }
}

.button {
  background-image: linear-gradient(0deg, #1eacc7 0%, #4ce2ff 100%);
  box-shadow: 1px 1px 0px 0px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  font-size: 14px;
  border: none;
  font-weight: 400;
  text-transform: uppercase;
  color: #ffffff;
  @include btn_hover();
  &_main {
    margin-top: 19px;
    width: 178px;
    height: 52px;
  }
  &_larg {
    width: 265px;
    height: 69px;

    display: block;
    margin: 118px auto 0 auto;
  }
}
